.odc-monitoring-overview__metric-accordion {
  padding: 0;
}

.odc-monitoring-overview__event-warning-toggle .pf-c-accordion__toggle-text {
  width: 100%;
}

.odc-monitoring-overview__event-warning-body .pf-c-accordion__expanded-content-body {
  padding: var(--pf-global--spacer--sm) 0;
}

.odc-monitoring-overview__view-monitoring-dashboard {
  margin: var(--pf-global--spacer--sm) 0;
  text-align: right;
}

.odc-monitoring-overview .pf-c-accordion__expanded-content.pf-m-expanded {
  --pf-c-accordion__expanded-content--BorderLeftColor: transparent;
}

.odc-monitoring-overview .pf-c-accordion__toggle.pf-m-expanded {
  --pf-c-accordion__toggle--BorderLeftColor: transparent;
}

.odc-monitoring-overview__empty-state-icon {
  color: var(--pf-global--icon--Color--light);
  font-size: var(--pf-global--FontSize--4xl);
}

